import { FeatureList, FeatureStore } from '@app/features/types/Feature';
import { useCallback, useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import type { Storage } from 'webextension-polyfill';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export const useContentFeaturesList = () => {
  const [featureStore, setFeatureStore] = useState<FeatureStore>(
    {} as FeatureStore,
  );

  const mergeStores = useCallback(
    (store?: FeatureStore, featuresList?: FeatureList): FeatureStore => {
      if (!store) return {} as FeatureStore;
      return {
        ...store,
        featuresList: {
          ...store.featuresList,
          ...(featuresList || ({} as FeatureList)),
        },
      };
    },
    [],
  );

  const updateFeatureStore = useCallback(() => {
    browser_api.storage.local
      .get([LocalStorageKey.FeatureStore, LocalStorageKey.FeaturesListLocal])
      .then((result) => {
        const store = result[LocalStorageKey.FeatureStore] as FeatureStore;
        const features = result[
          LocalStorageKey.FeaturesListLocal
        ] as FeatureList;
        const merged = mergeStores(store, features);

        setFeatureStore(merged);
      });
  }, [mergeStores]);

  useEffect(() => {
    updateFeatureStore();

    const listener = (changes: { [key: string]: Storage.StorageChange }) => {
      if (
        LocalStorageKey.FeatureStore in changes ||
        LocalStorageKey.FeaturesListLocal in changes
      ) {
        updateFeatureStore();
      }
    };

    browser_api.storage.local.onChanged.addListener(listener);

    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, [updateFeatureStore]);

  return featureStore;
};
